@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header{
    background-color:rgb(21, 56, 56);
    border-radius: 5px;
    width: 360px;
    height: 60px;
    color: white;
    font-family: 'New Tegomin', serif;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 20px auto ;
    box-shadow: rgb(41, 80, 53) 0 0 15px 5px;  
    -webkit-animation: mymove 4s;  
            animation: mymove 4s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;

}
@-webkit-keyframes mymove {
    0% {color: white;}
    25% {color: rgb(215, 255, 71);}
    50% {color: rgb(99, 71, 255);}
    75% {color: rgb(249, 71, 255);}
    100% {color: rgb(255, 71, 71);}
  }
@keyframes mymove {
    0% {color: white;}
    25% {color: rgb(215, 255, 71);}
    50% {color: rgb(99, 71, 255);}
    75% {color: rgb(249, 71, 255);}
    100% {color: rgb(255, 71, 71);}
  }
.App{
    height: 100vh;
    background-image: url(/toDoList/static/media/back.b49ddbf4.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;


}
.PlanList{
    background-color: rgb(21, 56, 56);
    border-radius: 5px;
    opacity: 0.9;
    width: 400px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    height: 90%;
    margin-top: 20px;
    font-family: 'Exo', sans-serif;
    letter-spacing: 3px;

  
}
.Plan{
    border-radius: 5px;
    width: 360px;
    height: 30px;
    color:tomato;
    margin: 5px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.buttons i{
    padding: 5px;
    background-color: transparent;
    font-size: 16px;
}
.buttons button{
    background-color: transparent;
    border: none;
    color: tomato;
    outline: none;

}
.AddPlan input{
    padding: 10px;
    width: 260px;
    border-radius:5px;
    border:solid black 1px;
    font-family: 'New Tegomin', serif;
    background-color: rgb(7, 5, 5);
    box-shadow: rgb(1, 59, 19) 0 0 15px 5px; 
    color: white;
    outline: none;
}
.AddPlan button{
    padding: 10px;
    border-radius:5px;
    border:solid tomato 1px; 
    margin-left: 5px;
    background-color: tomato;
    color: white;
    font-family: 'New Tegomin', serif;
    box-shadow: rgb(11, 230, 77) 0 0 15px 5px; 
    outline: none;
}
.AddPlan{
    text-align: center;
    margin-bottom: 20px;
  
}
.no-plans{
    text-align: center;
    color: tomato;
}
